import uniStatusBar from "@/components/uni-status-bar/uni-status-bar"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default{
	components:{
		uniStatusBar,
		uniLoadMore
	},
	data(){
		return{
			type:'',
			categoryReady:false,
			url:'&r=api.home.morecate',
			url2:'&r=api.home.morecate.catemerch',
			tapped:'',
			category:[],
			active:1,
			tabActive:0,
			sh:'',
			dataList:[],
			page:1,
			pageSize:20,
			total:0,
			more:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   onLoad(p){
		   var that=this
		   this.type=p.type
		   this.getCategory()
	   },
	methods:{
		toggleTab(t,item){
			this.tabActive=t
			this.tapped=item.id
			this.reset()
			this.getList('',item.id)
		},
		to(w,id1,id2){
			var cateId,goodsId
			if(this.active==1){
				cateId=this.category[id1].id
				goodsId=id2
			}else{
				goodsId=this.category[id1].id
				cateId=id2
			}
			uni.navigateTo({
				url:'./goodsList?cateId='+cateId+'&goodsId='+goodsId
			})
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		toggle(t){
			this.active=t
			this.category=[]
			this.tapped=false
			this.reset()
			this.getCategory()
		},
		reset(){
			if(this.active==1){
				 this.url='&r=api.home.morecate'
				 this.url2='&r=api.home.morecate.catemerch'
			}else{
				 this.url='&r=api.home.morecate.brand'
				this.url2='&r=api.home.morecate.brandcates'
			}
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getCategory(){
			var that=this
			  var params={
			  	   id:''
			  }
			  this.$apiPost(this.url,params).then((res) =>{
				  that.category=res.data
				  that.tabActive=0
				  that.categoryReady=true
			res.data.length>0 && that.getList('',res.data[0].id || 0)
			  })
		},
		getList(p,id){
			var that=this
			// var params={
			//   page:p,
			//   pagesize: this.pageSize
			// }
			// if(this.page==1){
			// 	this.$loading()
			// }
			  // var params={
			  // 	   id:id
			  // }
			  
			  if(this.active==1){
				  this.url2+='&cateid='+id
			  }else{
				  this.url2+='&brandid='+id
			  }
			  this.$apiPost(this.url2).then((res) =>{
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  // if(that.page==1){
				  // 	uni.hideLoading()
				  // }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page,id)
		  // },2000)
		},
	}
}
